<template>
  <div class="loss-distribution">
    <SmallModule title="损失分布" position="right">
      <template #content>
        <div class="content-layout">
          <NtgaleChart
            id="omChart"
            :width="`4.3rem`"
            :height="`2rem`"
            :data="NtgaleData"
            :radius="['30%', '80%']"
            showLabel
            @clickItem="handleLossPower"
          />
        </div>
      </template>
    </SmallModule>
  </div>
</template>

<script lang="ts" setup>
  import SmallModule from '/@/components/Framework/OperationScreen/SmallModule.vue';
  import NtgaleChart from '/@/components/Framework/Chart/NtgaleChart.vue';
  import { ref, reactive, UnwrapRef, onMounted } from 'vue';
  import { getLossElecStatData } from './data';
  import { assign } from 'lodash-es';
  import { addTabPage } from '/@/utils/route';

  function handleLossPower() {
    addTabPage('/bi/elec/lossPower');
  }

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  interface Props {
    overviewSearchInfo?: {};
  }
  const props = withDefaults(defineProps<Props>(), {});
  onMounted(() => {
    assign(searchInfo, props.overviewSearchInfo);
    queryLossElecStatData();
  });

  const NtgaleData = ref([]);

  const queryLossElecStatData = async () => {
    let data = await getLossElecStatData(searchInfo);
    if (data.result) {
      NtgaleData.value = data.result;
    }
  };
</script>

<style lang="less" scoped>
  .content-layout {
    width: 100%;
    height: 2.69rem;
    padding: 0.4rem 0.14rem 0;
  }
</style>
